// walletManager.ts

// Extend global window for wallet compatibility
export {};

declare global {
  interface Window {
    // MetaMask (Ethereum)
    ethereum?: any;

    // Polkadot.js extension
    injectedWeb3?: {
      [key: string]: {
        enable: (originName: string) => Promise<InjectedExtension>;
      };
    };
  }
}

export interface InjectedExtension {
  signer: {signPayload: (payload: any) => Promise<any>;};
  accounts: { get: () => Promise<{ address: string }[]>;
};
}












export async function connectWallet(chain: string): Promise<string | null> {
  if (chain.toLowerCase() === "ethereum") {
    if (!window.ethereum) throw new Error("MetaMask not available");

    const accounts: string[] = await window.ethereum.request({
      method: "eth_requestAccounts",
    });
    return accounts[0] ?? null;

  } 
  
  
  
  
  if (chain.toLowerCase() === "polkadot") {
    if (!window.injectedWeb3 || !window.injectedWeb3.polkadotjs) {
      throw new Error("Polkadot.js extension not available");
    }

    const extension = await window.injectedWeb3.polkadotjs.enable("YourApp");
    const accounts = await extension.accounts.get();
    return accounts[0]?.address ?? null;

  }
  
  
  return 'undefined'

}

export async function signMessage(chain: string, message: string): Promise<any> {
  if (chain.toLowerCase() === "ethereum") {
    const from = await connectWallet("ethereum");
    return await window.ethereum.request({
      method: "personal_sign",
      params: [message, from],
    });

  }
  
  
  
  if (chain.toLowerCase() === "polkadot") {
    const extension = await window.injectedWeb3!.polkadotjs.enable("YourApp");
    const accounts = await extension.accounts.get();
    const account = accounts[0];
    return await extension.signer.signPayload({
      address: account.address,
      data: message,
      type: "bytes",
    });

  } 
  

}
